@font-face {
  font-family: 'Roboto Condensed';
  src: local('Roboto Condensed'), url('../../fonts/RobotoCondensed-Regular.ttf') format('truetype');
}

html, body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  padding: 0;
  border: 0;
  -webkit-print-color-adjust: exact;
} 
h1, h3 { margin: 0;}
h2 {
  color: #99CCFF;
  padding-bottom: 10px;
  border-bottom: 2px solid #7289a0;
  margin-bottom: 20px;
}
h3 { color: #2c3e50;}
#app {
  font-family: 'Raleway', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#resume {
  width: 100%;
}
.important { font-weight: bolder;}
ul {
  list-style-type: none;
  padding: 0;
}
li{
  display: flex;
  align-items: center;
  margin: .5rem 0;
}
li:before {
  content: "";
  margin-right: .5rem;
  background: #99CCFF;
  width: 10px; 
  height: 10px;
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}
.container {
  width: 75%;
  float: right;
  text-align: left;
}
.content{
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow: hidden;
  width: 25%;
  height: 100vh;
  float: left;
  background-image: url("./header/header_background.png");
  background-repeat: repeat-x;
  background-position: bottom;
  background-color: #7289a0;
}
/*Header & Footer component*/
#header {
  text-align: center;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-bottom: 100px;
}
#header_occupation {
  font-family: 'Roboto Condensed', 'Raleway', Helvetica, Arial, sans-serif;
  color: #2c3e50;
  font-size: 20px;
  font-weight: lighter;
}
img#avatar {
  border-radius: 50%;
  width: 125px;
  height: 125px;
  margin-bottom: 20px;
  border: 5px #fff solid;
}
#contact_info a{
  margin: 3px 3px;
  color: black;
}
img.powered {
  width: 25px;
  height: 25px;
  vertical-align: middle;
}
#about {
  margin-bottom: 50px;
}
/* Footer component */
#footer {
  width: 75%;
  text-align: center;
  background-color: #7289a0;
  padding-bottom: 10px;
  padding-top: 10px;
  background-image: url("./header/header_background.png");
  background-repeat: repeat-x;
  background-position: bottom;
  bottom: 0;
	position: fixed;
}
/*Experience component*/
.job_info{
  width: 100%;
  font-style: italic;
}
.duration {
  width: 50%;
  float: left;
}
.location {
  width: 50%;
  float: right;
  text-align: right;
}
.techno{
  text-align: right;
  font-weight: bolder;
  font-style: italic;
}
/*Skill component*/ 
#skill{ display: flex;}
div.column { flex: 50%;}
.skill-bar-container {
  position: relative;
  width: 95%;
  border-radius: 4px;
  background-color: #E5E5E5;
  margin-bottom: 0.5em;
}
.skill-bar {
  display: block;
  height: 5px;
}
.level4 { width: 60%; background-color: #CDE6FF;}
.level5 { width: 75%; background-color: #B3D9FF;}
.level6 { width: 80%; background-color: #90C8FF;}
.level7 { width: 85%; background-color: #77BCFF;}
.level8 { width: 90%; background-color: #5EAFFF;}
.level9 { width: 95%; background-color: #43A1FF;}

.st0{
  fill-opacity: 0;
}
.st1{
  fill:#00d8ff;
}
.st2{
  fill:none;
  stroke:#00d8ff;
  stroke-width:144.7456;
  stroke-miterlimit:10;
}

.skill img:hover {
	transform: rotate(-360deg);

}
.skill img {
	transition: 1s;
  width: 25px;
  height: 25px;
  margin-right: 10px;
}